.jg-momentum-scroll-hitbox {
    width: 100vw;
    pointer-events: none;
}

body>video {
    display: none;
}

section:nth-child(2n-1) {
    background-color: #2f4242;
}

section:nth-child(2n) {
    background-color: #3b5252;
}

.jg-main {
    height: auto;
    width: 100vw;
    max-width: 100%;
}

main .main-heading {
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: unset;
}

.selection {
    position: relative;
    z-index: 2;
    width: 50%;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.selection-inner {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: transparent;
    box-shadow: 0px 0px 0px 0px black;
    transition: all 0.5s ease;
    max-width: 100%;
    white-space: nowrap;
}

.selection-item:not(:first-child) {
    margin-top: 10px;
}

.selection-item {
    cursor: pointer;
    width: 200px;
    padding: 10px;
    text-align: center;
    border: 1px solid;
    color: #ffffff;
    text-decoration: none;
}

#about-me-video {
    position: relative;
    opacity: 0;
    filter: grayscale(100%);
    transition: opacity 0.5s ease, filter 3s ease, transform 1500ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
}

#about-me-video.active {
    opacity: 1;
    filter: grayscale(0%);
}

.about-me-video-container {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-me-video-opacitizer {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.33);
    z-index: 1;
}

section.about-me {
    background-color: #2f4242;
}

.jg-main-h1, .jg-main-h2, .selection-item {
    transform: perspective(400px) rotateX(90deg);
}

.jg-main-h1 {
    font-size: 72px;
    margin: 0;
    text-shadow: 0 0 10px rgba(0, 255, 255, 0.95);
}

.jg-main-h2 {
    font-size: 26px;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
    text-shadow: 0 0 10px rgba(0, 255, 255, 0.95);
}

.h2-expert {
    color: rgb(0 255 255 / 70%);
    text-shadow: 0 0 4px rgba(0, 255, 255, 0.95);
}

.about-me-inner {
    font-size: 60px;
    text-align: center;
    margin: auto;
    max-width: 1000px;
    min-height: calc(100vh + 200px);
    display: flex;
    flex-flow: column;
    justify-content: center;
}

.about-me-inner>p {
    margin: 0;
}

section.skills {
    min-height: 100vh;
    position: relative;
    cursor: grab;
    background: linear-gradient(#3b5252, #161a1a);
}

section.skills:active {
    cursor: grabbing;
}

section.my-work {
    position: relative;
}

section.skills:before, section.my-work:before {
    font-size: 42px;
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    background-color: #383c3c;
    padding: 10px;
    text-align: center;
    z-index: 1;
}

section.skills:before {
    content: "My Tech Stack";
}

.skills-inner-shadow {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.skills-inner-shadow:after {
    content: "";
    display: block;
    position: absolute;
    bottom: -6px;
    left: 0;
    height: 2px;
    width: 100%;
    box-shadow: 1px -10px 10px 2px #484848;
}

section.my-work:before {
    content: "My Work";
}

.sect-inner-container {
    max-width: 1920px;
    margin: auto;
    padding: 100px 40px;
}

section.my-work .sect-inner-container {
    max-width: 1200px;
}

.mobile-my-work {
    display: none;
}

.my-work-item {
    display: flex;
}

.my-work-item:not(:last-child) {
    margin-bottom: 100px;
}

.my-work-item-image-container {
    height: auto;
    width: 50%;
    margin-top: auto;
    margin-right: 40px;
    margin-bottom: auto;
}

.my-work-item-image-container>img {
    max-width: 100%;
}

.my-work-item-desc {
    flex: 1 1;
    display: flex;
    flex-flow: column;
    justify-content: center;
    font-size: 32px;
}

.my-work-item-inner-desc {
    font-size: 20px;
    margin-top: 20px;
}

.my-work-item-inner-link {
    cursor: pointer;
    width: 200px;
    padding: 10px;
    text-align: center;
    border: 1px solid;
    color: #ffffff;
    text-decoration: none;
    font-size: 16px;
    margin-top: 20px;
}

.my-work-item-inner-link, .selection-item {
    transition: background-color 0.2s ease;
}

.my-work-item-inner-link:hover, .selection-item:hover {
    background-color: #00ffff80;
    color: #ffffff;
}

.desktop-my-work {
    width: 100%;
}

.desktop-my-work-item-background {
    width: 100%;
    height: 100vh;
    background-size: auto;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center right 70%;
}

.desktop-my-work-item {
    max-width: 50%;
    min-height: 100vh;
}

.desktop-my-work-item-info-txt {
    font-size: 24px;
    color: #eee;
    line-height: 1.5;
}

.flag-for-the-desktop-my-work .desktop-my-work-item {
    margin-left: auto;
    display: flex;
    align-items: center;
}

.desktop-my-work-item-info-btn {
    display: inline-block;
}

.desktop-my-work-item-info-heading {
    font-size: 4em;
}

.desktop-my-work-item-info {
    max-width: 515px;
    padding-left: 15px;
}

section.contact-me {
    padding: 40px;
    text-align: center;
    font-size: 24px;
}

.social-media {
    margin-top: 30px;
}

@media (min-width: 5000px) {
    .desktop-my-work-item-bg-img {
        background-position: center right 60%;
    }
}

@media (min-width: 1921px) {
    #about-me-video {
        width: 100%;
    }
}

@media (min-width: 992px) {
    .selection-inner.active {
        box-shadow: 0px 0px 12px rgba(0, 255, 255, 0.5);
        border: 1px solid rgba(127, 255, 255, 0.25);
        padding: 40px 80px;
        background-color: #2f4242a0;
    }
    section.my-work {
        padding: 0;
    }
    section.my-work .sect-inner-container {
        max-width: none;
        padding: 0;
    }
    .desktop-my-work-item-background:nth-child(2n) {
        /* background-color: #3b5252; */
        background-color: #2f4242;
    }
    section.contact-me {
        /* background-color: #2f4242; */
        background-color: #3b5252;
    }
}

@media (max-width: 1430px) {
    .desktop-my-work-item-bg-img {
        background-position: center right 85%;
    }
}

@media (max-width: 991px) {
    section.skills {
        pointer-events: none;
    }
    section.skills #container .element {
        pointer-events: none !important;
    }
    .desktop-my-work {
        display: none;
    }
    .mobile-my-work {
        display: block;
    }
    #about-me-video {
        transform: none !important;
    }
    .about-me-video-container {
        z-index: -1;
        width: 100%;
        height: 100vh;
        top: initial;
        right: initial;
        bottom: initial;
        left: initial;
    }
    .jg-momentum-element, #about-me-video {
        transition: none;
    }
    /* disable momentum scrolling on mobile */
    .jg-momentum-scroll-container {
        position: static;
        height: auto;
    }
    .jg-momentum-element {
        position: static;
    }
    .jg-momentum-scroll-hitbox {
        display: none;
    }
}

@media (max-width: 768px) {
    .about-me-video-opacitizer {
        background: rgba(0, 0, 0, 0.53);
    }
    .selection {
        width: 100%;
    }
    #about-me-video {
        right: 55%;
    }
    .my-work .sect-inner-container {
        padding: 100px 0;
    }
    .my-work-item {
        flex-flow: column;
    }
    .my-work-item-image-container {
        width: 100%;
    }
    .my-work-item-desc {
        margin-top: 10px;
        text-align: center;
        align-items: center;
    }
}

@media (max-width: 460px) {
    .jg-main-h1 {
        font-size: 60px;
    }
    .jg-main-h2 {
        white-space: nowrap;
    }
    #about-me-video {
        right: 100%;
    }
    .about-me-inner {
        font-size: 48px;
    }
    section.contact-me {
        font-size: 20px;
    }
    section.skills:before, section.my-work:before {
        font-size: 32px;
    }
    .my-work-item-inner-desc {
        font-size: 16px;
    }
}