.jg-horizon-scroll-container.the-show-begins:after {
    opacity: 1;
}

.jg-horizon-horizontal-container {
    display: flex;
    position: sticky;
    top: 0;
    transition: transform 1500ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
    z-index: 1;
}

.jg-horizon-horizontal-item {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.jg-horizon-horizontal-item-inner {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 15vh;
}

.jg-horizon-horizontal-item-image-container {
    height: 100%;
    position: relative;
    overflow: hidden;
    cursor: pointer;
}

.jg-horizon-horizontal-item-image-container:before {
    content: "";
    z-index: 10;
    position: absolute;
    height: 200%;
    width: 200%;
    top: -120%;
    left: -120%;
    background: linear-gradient(transparent 0%, rgba(255, 255, 255, 0.1) 45%, rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0.1) 55%, transparent 100%);
    transform: rotate(-45deg);
}

.jg-horizon-horizontal-item-image-container:hover:before {
    top: 100%;
    left: 100%;
    transition: top 2s, left 2s;
}

.jg-horizon-horizontal-item-image-container:after {
    content: "Click to Read More";
    position: absolute;
    height: 100%;
    width: 100%;
    display: block;
    top: 0;
    background: #000000dd;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 32px;
    opacity: 0;
    transition: opacity 1000ms ease;
}

.jg-horizon-horizontal-item-image-container:hover:after {
    transition-delay: 5000ms;
    opacity: 1;
}

.jg-horizon-horizontal-item-image {
    background-image: url(/static/media/hacker-js-basic-cert.375f4060.png);
    height: 100%;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    cursor: pointer;
}

.jg-horizon-x-container {
    position: fixed;
    font-size: 32px;
    right: 20px;
    top: -100px;
    z-index: 2;
    cursor: pointer;
    transition: top 800ms;
}

.the-show-begins .jg-horizon-x-container {
    top: 20px;
}

.jg-horizon-selector {
    height: 10vh;
    display: inline-flex;
    z-index: 1;
    position: fixed;
    top: calc(120% - 20px);
    left: 50%;
    transform: translate(-50%, -100%);
    transition: top 800ms;
}

.the-show-begins .jg-horizon-selector {
    top: calc(100% - 20px);
}

.jg-horizon-selector-item {
    height: 100%;
    width: 100px;
}

.jg-horizon-info-modal {
    position: fixed;
    top: -100%;
    left: 0;
    height: 100vh;
    width: 100vw;
    background: #000000dd;
    z-index: 2;
    font-size: 32px;
    line-height: 1.5;
    padding: 40px;
    transition: top 0.6s ease-out;
    overflow: auto;
}

.jg-horizon-info-modal.active {
    top: 0;
}

.jg-horizon-info-modal-x {
    display: inline-block;
    cursor: pointer;
}

.jg-horizon-info-modal-x-container {
    display: flex;
    justify-content: flex-end;
}

.jg-horizon-info-modal-content li {
    margin-bottom: 20px;
}