/*  
1. WEB COLORS
2. FLIP IN X ANIMATION
3. GLOBAL LOADER
4. VERTICAL BARS LOADING
5. BACK TO TOP
6. MOMENTUM SCROLL
*/

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #3b5252;
    color: #eeeeee;
    font-family: 'Raleway';
    display: flex;
    justify-content: center;
}

section {
    padding: 0 15px;
}

#root {
    max-width: 1920px;
    overflow-x: hidden;
}

.display-block {
    display: block !important;
}

.display-none {
    display: none !important;
}

.position-relative {
    position: relative;
}

.overflow-hidden {
    overflow: hidden !important;
}

.opacity-1 {
    opacity: 1 !important;
}

/* WEB COLORS */

.bg-blue-dark {
    background-color: #021373;
}

.text-blue-dark {
    color: #021373;
}

.bg-blue-middark {
    background-color: #0540f2;
}

.text-blue-middark {
    color: #0540f2;
}

.bg-blue-midlight {
    background-color: #0554f2;
}

.text-blue-midlight {
    color: #0554f2;
}

.bg-blue-light {
    background-color: #0583f2;
}

.text-blue-light {
    color: #0583f2;
}

.bg-black {
    background-color: #0d0d0d;
}

.text-black {
    color: #0d0d0d;
}

/* FLIP IN X ANIMATION */

/* @keyframes flipInX {
    0% {
        transform: perspective(400px) rotateX(90deg);
        animation-timing-function: ease-in;
        opacity: 0
    }
    40% {
        transform: perspective(400px) rotateX(-20deg);
        animation-timing-function: ease-in
    }
    60% {
        transform: perspective(400px) rotateX(10deg);
        opacity: 1
    }
    80% {
        transform: perspective(400px) rotateX(-5deg)
    }
    to {
        transform: perspective(400px)
    }
} */

.flipInXInitial {
    transform: perspective(400px) rotateX(90deg);
}

@keyframes flipInX {
    0% {
        transform: perspective(400px) rotateX(90deg);
        animation-timing-function: ease-in;
        opacity: 0
    }
    40% {
        transform: perspective(400px);
        animation-timing-function: ease-in
    }
    60% {
        transform: perspective(400px);
        opacity: 1
    }
    80% {
        transform: perspective(400px)
    }
    to {
        transform: perspective(400px)
    }
}

.flipInX {
    backface-visibility: visible!important;
    animation-name: flipInX
}

.animated {
    animation-duration: 1s;
    animation-fill-mode: both
}

.animated.infinite {
    animation-iteration-count: infinite
}

.animated.delay-1s {
    animation-delay: 1s
}

.animated.delay-2s {
    animation-delay: 2s
}

.animated.delay-3s {
    animation-delay: 3s
}

.animated.delay-4s {
    animation-delay: 4s
}

.animated.delay-5s {
    animation-delay: 5s
}

.animated.fast {
    animation-duration: .8s
}

.animated.faster {
    animation-duration: .5s
}

.animated.slow {
    animation-duration: 2s
}

.animated.slower {
    animation-duration: 3s
}

@media (prefers-reduced-motion:reduce), (print) {
    .animated {
        animation-duration: 1ms!important;
        transition-duration: 1ms!important;
        animation-iteration-count: 1!important
    }
}

/* GLOBAL LOADER */

.global-loader {
    opacity: 0;
    pointer-events: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: 1031;
    justify-content: center;
    align-items: center;
    background-color: #2f4242;
    transition: opacity 0.8s ease;
}

.global-loader.dark {
    background-color: rgba(0, 0, 0, 0.8) !important;
}

.global-loader.light {
    background-color: rgba(255, 255, 255, 0.8) !important;
}

.global-loader.ajax-in-progress {
    pointer-events: unset;
    display: flex;
    opacity: 1;
}

/* VERTICAL BARS LOADING */

.vert-bars-loading {
    height: 30px;
    display: none;
    align-items: center;
}

.ajax-in-progress .vert-bars-loading {
    display: flex;
}

.modal-footer>.vert-bars-loading:not(:last-child) {
    margin-right: auto;
}

.vert-bars-item {
    display: block;
    width: 10px;
    background-color: #ffffff;
    transform: rotate(180deg);
    align-self: flex-end;
}

.vert-bars-txt {
    padding-left: 10px;
}

.vert-bars-item:nth-child(1) {
    animation: vert-bars-item-animation 0.3s ease alternate infinite;
}

.vert-bars-item:nth-child(2) {
    animation: vert-bars-item-animation 0.3s ease 0.1s alternate infinite;
}

.vert-bars-item:nth-child(3) {
    animation: vert-bars-item-animation 0.3s ease 0.2s alternate infinite;
}

@keyframes vert-bars-item-animation {
    from {
        height: 0%;
    }
    to {
        height: 100%;
    }
}

/* BACK TO TOP */

.BackToTop {
    position: fixed;
    bottom: 2%;
    right: 2%;
    font-size: 50px;
    border: 1px solid #ffffff;
    border-radius: 50%;
    width: 50px;
    text-align: center;
    line-height: 1;
    opacity: 0;
    cursor: pointer;
    transition: opacity 0.3s ease;
}

.BackToTop.active {
    opacity: 0.5;
}

.BackToTop.active:hover {
    opacity: 1;
    background-color: #00ffff80;
}

/* MOMENTUM SCROLL */

.jg-momentum-scroll-container {
    position: fixed;
    width: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100vh;
    overflow: hidden;
}

.jg-momentum-element {
    position: relative;
    transition: top 1500ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
}